/* GENERAL */

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;-moz-box-sizing:content-box;-webkit-box-sizing:content-box;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

html {
  font-size: 62.5%;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  font-family: 'Crimson Text', serif;
  color: black;
}

ul {
  margin:0;
  padding: 0;
}

body {
  font-family: 'Crimson Text', serif;
}

.wrapper {
  width: 90%;
  max-width: 1800px;
  margin: 0 auto;
}

/* HEADER */

header nav {
  background: white;
  padding:20px 0;
}

header nav li:first-child {
  padding-left: 0;
}

header nav .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.title {
  text-align: center;
  font-size: 3.5rem;
}

.title span {
  color: darkorange;
  margin-left: 3px;
}

header nav ul {
  list-style-type: none;
  display: flex;
  text-transform: uppercase;
  font-size: 1.6rem;
  font-family: 'Nunito Sans', sans-serif;
}

.fixedHeader ul li {
  padding: 10px;
  border: 3px solid white;
}

.fixedHeader a {
  text-decoration: none;
  color:black;
}

.fixedHeader .hover {
  display: inline-block;
	position: relative;
}

.fixedHeader .hover a:focus{  
  color:orange;
  font-weight: bold;
}

.fixedHeader .hover::after {
  content: '';
	display: block;
	margin: auto;
	height: 3px;
	width: 0px;
	background: transparent;
	transition: width .5s ease, background-color .5s ease;
}

.fixedHeader .hover:hover:after {
	width: 100%;
	background: darkorange;
}


/* FILTERS */

.categories h2 {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.filter {
  display: flex;
  justify-content: center;
}

.categories {
  margin-bottom: 25px;
  background-color: #f5f5f5;
  padding: 75px;
}

select {
  border-radius: 3px;
  font-size: 1.6rem;
  box-shadow: 0px 1rem 2rem -1.5rem;
  padding: 5px;
  font-family: 'Crimson Text', serif;
  border: none;
  height: 40px;
  width: 200px;
}

select:focus,
button [type="submit"]:focus  {
  color: darkorange;
}
  
button[type="submit"]{
    padding: 5px;
    font-size: 1.6rem;
    border-radius: 5px;
    font-family: 'Crimson Text', serif;
    text-transform: uppercase;
    margin: 0 15px;
    padding: 5px 12px;
    box-shadow: 0px 1rem 2rem -1.5rem;
}


/* HERO */

.hero {
  text-align: center;
  height: 80vh;
  background-color: darkorange;
  background: url(./assets/hero.jpg);
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.hero h2 {
  text-transform: uppercase;
  font-size: 2.3rem;
}

.hero h3 {
  font-size: 2rem;
}

.hero .text {
  background: white;
  width: 500px;
  border-radius: 25px;
}

/* BOOKS */

.mainPage .fixedHeader a:last-child {
  color: orange;
  font-weight: bold;
}

.books ul {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}

.books li {
  margin: 8px auto;
  display:flex;
  flex-direction:column;
  justify-content: space-between;
  width: 100%;
  max-width: 245px;
  min-height: 350px;
}

.books li a {
  text-decoration: none;
}

.books .title {
  font-size: 1.9rem;
  text-transform: uppercase;
  margin: 10px 0 5px 0;
  font-family: 'Nunito Sans', sans-serif;
}

.books .author {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.7rem;
  text-align: center;
}

.books img {
  width: 100%;
  max-width: 250px;
  min-height: 350px;
}

/* ABOUT PAGE & BLOG PAGE*/

.aboutPage .hero,
.blogPage .hero {
    height: 35vh;
}

.aboutPage .fixedHeader a:first-child,
.blogPage .fixedHeader a:nth-child(2) {
  color: orange;
  font-weight: bold;
}

/* ABOUT SECTION */

.aboutSection{
  text-align: center;
  margin-top: 35px;
  width: 60%;
}

.aboutPage h2 {
  font-size: 2.5rem;
  margin-bottom: 1.6rem;
  text-transform: uppercase;
}

.aboutPage p {
  font-size: 2rem;
  line-height: 3.5rem;
  margin-bottom: 1.6rem;
}

/* BLOG PAGE*/


.blogPage .blogTitle {
  font-size: 3.5rem;
  text-transform: uppercase;
  text-align: center;
  border-bottom: 2px solid orange;
  padding-bottom: 2.5rem;
}

.blogSection {
  margin-top: 35px;
  width: 60%;
}

.blogInfo {
  padding-bottom: 1.6rem;
  margin-bottom: 2rem;
}

.blogSpecifics {
  display: flex;
  justify-content: space-between;
}

.blogPage h2 {
  font-size: 3rem;
  margin-bottom: 30px;
  line-height: 4.1rem;
  text-align: center;
  padding-top: 4rem;
}

.blogPage h3 {
  text-transform: uppercase;
  font-size: 2.5rem;
  margin-bottom: 1.6rem;
}

.blogPage h4 {
  font-size: 2.1rem;
  text-transform: uppercase;
}

.blogPage p {
  font-size: 2rem;
  line-height: 3.5rem;
  margin-bottom: 1.6rem;
}

/* BOOK PAGE */

.bookSection {
  width: 60%;
  margin-top: 65px;
}

.productInfo {
  display: flex;
  justify-content: space-around;
  flex-direction: row-reverse;
  margin-bottom: 50px;
}

.productInfo .image{
  width: 50%;
}

.productInfo img {
  width: 100%;
}

.productInfo .text {
  width: 50%;
  margin-left: 50px;
  border-left: 2px solid orange;
  padding-left: 25px;
}

.bookSection h1 {
  font-size: 3rem;
  margin-top: 15px;
}

.bookSection h2 {
  font-size: 2.4rem;
  margin-bottom: 1.6rem;
}

.bookSection h3{
  font-size: 2.1rem;
  text-transform: uppercase;
  margin-bottom: 1.8rem;
}

.bookSection p {
  font-size: 1.8rem;
}

/* FOOTER */

.footer {
  background-color: #fafafa;
  margin-top: 50px;
}

.footer .wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 25px 0;
}

.attribution {
  display: flex;
  flex-direction: column;
}

.footer .attribution a {
  color: black;
}

.footer a {
  padding-top: 15px;
  font-size: 1.6rem;
  text-decoration: none;
  color: darkorange;
}


/* MEDIA QUERIES */

@media(max-width: 725px){
  .blogSpecifics{
    flex-direction: column;
  }

  .blogPage h2 {
    text-align: left;
  }
}

@media(max-width: 470px) {

  .title,
  header nav ul {
    margin: 0 auto
  }

  .aboutPage p,
  .blogPage p {

    line-height: 3rem;
  }

  .hero {
    background-image: url(assets/heroMobile.jpg);
    /* background-size: contain; */
  }
}
